<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="2">
                <el-button type="info" @click="addinfo" >添加</el-button>
            </el-col>
            <el-col :span="16">
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="审批人">
                        <el-input v-model="formInline.user" placeholder="审批人"></el-input>
                    </el-form-item>
                    <el-form-item label="活动区域">
                        <el-select v-model="formInline.region" placeholder="活动区域">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="getlist">查询</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            fixed
            prop="date"
            label="日期">
            </el-table-column>
            <el-table-column
            prop="name"
            label="姓名"
            width="120">
            </el-table-column>
            <el-table-column
            prop="province"
            label="省份"
            width="120">
            </el-table-column>
            <el-table-column
            prop="city"
            label="市区"
            width="120">
            </el-table-column>
            <el-table-column
            prop="address"
            label="地址">
            </el-table-column>
            <el-table-column
            prop="zip"
            label="邮编"
            width="120">
            </el-table-column>
            <el-table-column
            fixed="right"
            label="操作">
            <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                <el-button @click="handlechange(scope.row)" type="text" size="small">编辑</el-button>
                <el-button @click="delinfo(scope.row)" type="text" size="small">删除</el-button>
            </template>
            </el-table-column>
        </el-table>

        <!-- 添加&修改 -->
        <el-dialog
        title="编辑"
        :visible.sync="dialogVisible"
        width="50%">
        <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="日期">
                <el-date-picker
                v-model="form.date"
                type="date"
                format="yyyy 年 MM 月 dd 日"
                value-format="yyyy-MM-dd"
                >
                </el-date-picker>
            </el-form-item>
            <el-form-item label="姓名">
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="省份">
                <el-input v-model="form.province"></el-input>
            </el-form-item>
            <el-form-item label="市区">
                <el-input v-model="form.city"></el-input>
            </el-form-item>
            <el-form-item label="地址">
                <el-input v-model="form.address"></el-input>
            </el-form-item>
            <el-form-item label="邮编">
                <el-input v-model="form.zip"></el-input>
            </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="hanldlesub">确 定</el-button>
        </span>
        </el-dialog>
        
    </div>
  
</template>

<script>
import api from '@/api/login'
  export default {
    methods: {
      handleClick(row) {
        console.log(row);
      },
      onSubmit() {
        console.log('submit!');
      },
      addinfo(){
          this.form={
            id:null,
            date:'',
            name: '',
            province: '',
            city: '',
            address: '',
            zip: ""
        }
          this.dialogVisible=true;
      },
      hanldlesub(){
          let _this=this;
          console.log(this.form)
          if(this.form.id){
            api.tableup(this.form).then(function (response) {
                _this.$message({
                    showClose: true,
                    message: response.data
                });
                _this.dialogVisible = false;
                _this.getlist()
            }).catch(function (error) {
                // console.log(error);
            });  
          }else{
            api.tableadd(this.form).then(function (response) {
                _this.$message({
                    showClose: true,
                    message: response.data
                });
                _this.dialogVisible = false;
                _this.getlist()
            }).catch(function (error) {
                // console.log(error);
            });
          }
          
      },
      getlist(){
          let _this=this;
          api.gettable(this.formInline).then(function (response) {
              _this.tableData=response.data;
              
           }).catch(function (error) {
                // console.log(error);
           });
      },
      handlechange(row){
        this.form={
            id:row.id,
            date:row.date,
            name:row.name,
            province:row.province,
            city:row.city,
            address:row.address,
            zip:row.zip
        }
          this.dialogVisible=true;
      },
      delinfo(row){
          let _this=this;
          api.deltable({rows:row.id}).then(function (response) {
              
               _this.getlist()
          }).catch(function (error) {
                // console.log(error);
          });
      }
    },
    created() {
        this.getlist()
    },
    mounted() {
    },
    data() {
      return {
        dialogVisible:false,
        formInline: {
          user: '',
          region: ''
        },
        form:{
            id:null,
            date:'',
            name: '',
            province: '',
            city: '',
            address: '',
            zip: ""
        },
        tableData: [{
          date: '2016-05-03',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-02',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-04',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-01',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }]
      }
    }
  }
</script>


