import get from '@/utils/get'
import post from '@/utils/post'
const qs = require('querystring');
export default {
    gettable(data){
        return get({
            url : 'table/tablelist',
            params : data
        })
    },
    login(data){
        return post({
            url : 'users/login',
            data : qs.stringify(data) 
        })
    },
    addreg(data){
        return post({
            url : 'users/add',
            data : qs.stringify(data) 
        })
    },
    tableadd(data){
        return post({
            url : 'table/addinfo',
            data : qs.stringify(data) 
        })
    },
    tableup(data){
        return post({
            url : 'table/upinfo',
            data : qs.stringify(data) 
        })
    },
    deltable(data){
        return post({
            url : 'table/delinfo',
            data : qs.stringify(data) 
        })
    },
}




